import React from "react";
import './style.css'
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';
import Grid from '@mui/material/Grid';
import DatePicker from 'react-date-picker';
import Button from '@mui/material/Button';
import ClearIcon from '@mui/icons-material/Clear';
import AddIcon from '@mui/icons-material/Add';
import RemoveIcon from '@mui/icons-material/Remove';

const DailyExpense = ({itemsOfSelectedDate})=>{

    const [value, onChange] = React.useState(new Date());
    const [filter, setFilter] = React.useState('')
    const [hide, setHide] = React.useState(true)

    const filteredList = itemsOfSelectedDate.filter(item=>item.category.includes(filter))
    const total = filteredList.map(item=>item.amount)
    
    const toggleHide = ()=>{
        setHide(!hide)
    }

    const changeFilterValue = (event)=>{
        setFilter(event.target.value)
    }

    const clearFilter = ()=>{
        setFilter('')
    }
    
    return (
        <Grid container direction="column">
            <Grid item>
            {`Add to date: `}
            <DatePicker
                    onChange={onChange}
                    value={value}
                    maxDate = {new Date()}
                    clearIcon = {null}
                    format = 'dd-MM-y'
                />
            </Grid>
            <div className = 'box1'>
            <Grid container alignItems="center"  className='borderBottom1'>
                <Grid item xl ={6} md={6} xs={12} className='padding0'>
                    <Grid className = 'marginB5'><span className = 'expenseDate'>{ value && (value.getDate() === new Date().getDate()? 'Todays':value.toDateString())} Expense<Button size='small' onClick={toggleHide}>{hide? <AddIcon size='small'/>:<RemoveIcon size='small'/> }</Button></span></Grid>
                </Grid>
                <Grid item xl ={3} md={3} xs={8}>
                    <FormControl  fullWidth size="small">
                        <InputLabel id="demo-simple-select-label">Category</InputLabel>
                        <Select
                            labelId="demo-simple-select-label"
                            id="demo-simple-select"
                            value={filter}
                            label="category"
                            onChange={changeFilterValue}
                        >
                            <MenuItem value={"Food"}>Food</MenuItem>
                            <MenuItem value={"Transport"}>Transport</MenuItem>
                            <MenuItem value={"Entertainment"}>Entertainment</MenuItem>
                            <MenuItem value={"Utility"}>Utility</MenuItem>
                        </Select>
                    </FormControl>
                </Grid>
                <Grid item xl ={3} md={3} xs={4}>
                    <Button onClick={clearFilter}>Clear</Button>
                </Grid>
            </Grid>
            {hide?<div></div>:
                <Grid container>
                <Grid container direction = 'column' wrap = 'wrap' sx = {{height:'35vh', overflowX:'auto', width:'100%', marginTop:'5px'}}>
                    {
                        itemsOfSelectedDate.length ===0 && (
                            <Grid>
                                Add items to list here...
                            </Grid>
                        )
                    }
                    {
                        itemsOfSelectedDate && itemsOfSelectedDate.length>0 && 
                        filteredList.map((item, index)=>{
                        return <Grid item className='itemBox' >{`${index+1}. ${item["name"]} ${item["amount"]}`}<Button size='small' sx={{borderRadius:'20px'}}><ClearIcon/></Button> </Grid>
                    })
                    }
                </Grid>
            </Grid>}
            <Grid item sx={{margin:'0', marginRight:'10px', fontWeight:'500'}}>
                 {`Total ${filter} Amount: ${total.reduce((i, j )=>i+j,0)}`}
            </Grid>
            </div>
        </Grid>
    )
}


export default DailyExpense;