import React from "react";
import './style.css'
import Button from '@mui/material/Button';

const Nav = ()=>{
    return (
		<div>
			<ul className='NavUl'>
				<li className="header_logo" key='logoNav'>
					Expense Report
				</li>
				<li className = 'liNav' key='logOut'>
				<Button size = 'medium' color='error'>Log Out</Button>
				</li>
			</ul>
		</div>
    )
}

export default Nav;