import React, {useState} from "react";
import Grid from '@mui/material/Grid';
import Button from '@mui/material/Button';
import ClearIcon from '@mui/icons-material/Clear';
import './style.css'
import AddIcon from '@mui/icons-material/Add';
import RemoveIcon from '@mui/icons-material/Remove';

const IncomeExpenseBox =({title, itemsList=[]})=>{
    const [hide, setHide] = useState(true);

    const toggleHide = ()=>{
        setHide(!hide)
    }

    const amountList = itemsList.map(item=>item.amount)

    return (
        <Grid container className='box2' sx={{marginTop:'10px'}}>
            <Grid item xl={12} md = {12} xs={12} sx = {{fontWeight:'400', fontSize:'1.2em', borderBottom:'1px solid'}}>
               {title} <Button size='small' onClick={toggleHide}>{hide? <AddIcon size='small'/>:<RemoveIcon size='small'/> }</Button>
            </Grid>
            { hide?<div></div>:
                <Grid container direction = 'column' wrap = 'wrap' sx = {{height:'25vh', overflowX:'auto', width:'100%', marginTop:'5px'}}>
                {
                    itemsList.length === 0 && (<Grid>Add items to list here...</Grid>)
                    
                }
                {
                    itemsList && itemsList.length>0 && 
                    itemsList.map((item, index)=>{
                    return <Grid item className='itemBox' >{`${index+1}. ${item["description"].toUpperCase()} ${item["amount"]}`}<Button size='small' sx={{borderRadius:'25px'}}><ClearIcon/></Button> </Grid>
                })
                }
            </Grid>
            }
            <Grid item sx={{fontWeight:'500'}}>
                Total: {amountList.reduce((i,j)=>parseInt(i)+parseInt(j), 0)}
            </Grid>
        </Grid>
    )
}

export default IncomeExpenseBox;
