import React, {useState} from "react";
import AddExpenseBar from "../AddExpenseBar/AddExpenseBar";
import './style.css'
import ColumnChart from "../ColumnChart/ColumnChart";
import DailyExpense from "../DailyExpense/DailyExpense"
import Grid from '@mui/material/Grid';
import { toast } from 'react-toastify';
import MonthlyExpense from '../MonthlyExpense/MonthlyExpense'

const Section = ()=>{

    const [itemsOfSelectedDate, setItemsOfSelectedDate] = useState([])
    const [duplicateItemObj, setDuplicateItemObj] = useState({})

    const notifyError = (text) => toast.error(text);
    const notifySucess = (text)=> toast.success(text);

    const confirmedItems = (obj)=>{
        if (duplicateItemObj[obj.name] === undefined){
            setItemsOfSelectedDate([...itemsOfSelectedDate, obj])
            notifySucess('Success')
        }else{
            notifyError('Item already added')
        }
        let obj1 = {}
        obj1[obj.name] = 1
        setDuplicateItemObj({...duplicateItemObj, ...obj1})
    }

    const totalDaysAmount = itemsOfSelectedDate.map(i=>i.amount)

    return (
        <Grid sx = {{marginBottom:'10px'}}>
            <Grid container justifyContent='center' sx={{marginTop:'15px'}}>
                <Grid item xl={5} md={8} xs={12} className='height100'>
                    <AddExpenseBar confirmedItems={confirmedItems}/>
                    <DailyExpense itemsOfSelectedDate={itemsOfSelectedDate}/>
                </Grid>
            </Grid>
            <Grid container justifyContent='center' sx={{marginTop:'15px'}}>
                <Grid item xl={5} md={8} xs={12}>
                    <MonthlyExpense msgPopUp = {{notifyError:notifyError, notifySucess:notifySucess}} dayExpenseList = {totalDaysAmount}/>
                </Grid>
            </Grid>
        </Grid>
    )
}


export default Section;