import React, {useState} from "react";
import Grid from '@mui/material/Grid';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';
import IncomeExpenseBox from "../IncomeExpenseBox/IncomeExpenseBox";

const MonthlyExpense = ({msgPopUp, dayExpenseList})=>{
    const [type, setType] = useState('')
    const [description, setDescription] = useState('')
    const [amount, setAmount] = useState('')
    const [incomeList, setIncomeList] = useState([])
    const [billList, setBillList] = useState([])
    const [otherList, setOtherList] = useState([])

    const incomeAmountList = incomeList.map(i=>i.amount)
    const billAmountList = billList.map(i=>i.amount)
    const otherAmountList = otherList.map(i=>i.amount)


    const toggleType = (e)=>{
        setType(e.target.value)
    }
    const toggleDescription = (e)=>{
        setDescription(e.target.value)
    }
    const toggleAmount = (e)=>{
        setAmount(e.target.value)
    }
    const clearForm = ()=>{
        setType('')
        setDescription('')
        setAmount('')
    }

    const confirmSelection = ()=>{
        if (!amount || !description || !type){
            msgPopUp.notifyError('Select type, description and amount')
        }else{
            const obj = {
                type:type,
                amount:parseInt(amount)>0?parseInt(amount):(-1*parseInt(amount)),
                description:description
            }
            if (type === 'income'){
                setIncomeList([...incomeList, obj])
            }else if (type === 'bill'){
                setBillList([...billList, obj])
            }else if (type==='other'){
                setOtherList([...otherList, obj])
            }
            clearForm()
        }
        
    }

    return(
        <Grid>
            <Grid sx = {{fontWeight:'600', fontSize:'1.4em'}}>
                Monthly income and expense
            </Grid>
            <Grid container spacing={1} sx={{marginTop:'10px'}}>
            <Grid item xl={4} md={3} xs={6}>
                    <FormControl focused size='small' fullWidth color='secondary'> 
                        <InputLabel id="demo-simple-select-label">Type</InputLabel>
                        <Select
                            labelId="demo-simple-select-label"
                            id="demo-simple-select"
                            value={type}
                            label="type"
                            onChange={toggleType}
                        >
                            <MenuItem value={"income"}>Income</MenuItem>
                            <MenuItem value={"bill"}>Bill</MenuItem>
                            <MenuItem value={"other"}>Other Expenses</MenuItem>
                        </Select>
                    </FormControl>
                </Grid>
                <Grid item xl={3} md={3} xs={6}>
                    <TextField focused size='small' id="outlined-basic" label="Description" variant="outlined" value={description} onChange={toggleDescription}/>
                </Grid>
                <Grid item xl={3} md={3} xs={6}>
                    <TextField focused color='error' size='small' id="outlined-basic" label="Amount" variant="outlined" type='number' onChange={toggleAmount} value={amount} InputProps={{ inputProps: { min: 1 } }}/>
                </Grid>
                <Grid item xl={2} md={3} xs={6}>
                    <Button onClick = {confirmSelection}>Add</Button>
                </Grid>
            </Grid>
            <Grid>
                <IncomeExpenseBox title='Income' itemsList = {incomeList}/>
            </Grid>
            <Grid>
                <IncomeExpenseBox title='Bill'  itemsList = {billList}/>
            </Grid>
            <Grid>
                <IncomeExpenseBox title='Others'  itemsList = {otherList}/>
            </Grid>
            <Grid sx={{fontWeight:'600', marginTop:"10px"}}>
                Balance: {incomeAmountList.reduce((i,j)=>i+j, 0)-billAmountList.reduce((i,j)=>i+j, 0)-otherAmountList.reduce((i,j)=>i+j, 0)-dayExpenseList.reduce((i,j)=>i+j, 0)}
            </Grid>
        </Grid>
    )
}

export default MonthlyExpense;